<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="950"
    >
      <div slot="title">系统内商品</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">产品主图:</span>
            <a-input v-model="form.primary_image" style="width: 630px;" />
          </div>
        </div>

        <div class="row" v-if="type == 'add'">
          <div class="columns">
            <span class="label" style="width: 71px;">系统内sku:</span>
            <div style="display: flex; flex-direction: column;">
              
              <div>
                <span class="label" style="width: 40px;">sku前缀编号:</span>
                <a-input v-model="sku_front_name" style="width: 200px; margin-right: 5px;"/>
              </div>

              <div v-for=" attr,index in attrList" :key="index" style="display: flex; flex-direction: row; align-items: center; margin-bottom: 5px;" >
                <span class="label" style="width: 40px;">属性{{ index+1 }}:</span>
                <div v-for="attrValue,index_attr in attr" :key="index_attr">
                  <a-input v-model="attr[index_attr]" style="width: 50px; margin-right: 5px;"/>
                </div>
                <a-button icon="edit" size="small" @click="() => addAttr(attr)">添加属性</a-button>
                <a-button type="danger" icon="delete" size="small" @click="() => delAttr(attr)">删除属性</a-button>
              </div>
            </div>
            <a-button icon="edit" size="small"  @click="addAttrList">添加属性集</a-button>
            <a-button type="danger" icon="delete" size="small" @click="delAttrList">删除属性集</a-button>
          </div>
        </div>

        <div class="row" v-if="type == 'modify'">
          <div class="columns">
            <span class="label" style="width: 71px;">系统sku:</span>
            <a-input v-model="form.system_sku" style="width: 630px;"/>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 90px;">参考重量:</span>
            <a-input v-model="form.reference_weight" />
          </div>

          <div class="columns">
            <span class="label" style="width: 90px;">参考价格:</span>
            <a-input v-model="form.reference_cost" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">采购链接:</span>
            <div>
              <div v-for="link,index in purchase_links" :key="link+index">
                <span>链接{{ index+1 }}号. </span>
                <span>链接:</span>
                <a-input v-model="purchase_links[index].link" style="width: 150px; margin-right: 10px;"/> 
                <span>备注:</span>
                <a-input v-model="purchase_links[index].msg" style="width: 150px; margin-right: 10px;"/> 
                <a-button style="margin-right:10px; width: 95px;" size="small" @click="toTop(index)">设为主供应商</a-button>
                <a-button type="danger" icon="delete" size="small" @click="delpurchaseLink(index)">删除</a-button>
              </div>
            </div>
          </div>
          <a-button icon="edit" size="small" @click="addPurchaseLink">新增采购链接</a-button>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">采购平台:</span>
            <a-select v-model="form.purchase_platform" placeholder="请选择" style="width: 215px;"> 
              <a-select-option v-for="option in options" :value="option" :key="option">{{ option }}</a-select-option> 
            </a-select> 
          </div>

          <div v-if="type == 'modify'" class="columns">
            <span class="label" style="width: 115px;">供应商名称:</span>
            <a-input v-model="form.supplier_name" />
          </div>
        </div>

        <div class="row" v-if="type == 'modify'">
          <div class="columns">
            <span class="label" style="width: 71px;">是否同步:</span>
            <a-radio-group v-model="is_all">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
            <span style="color: red"> *参考重量、参考价格、采购链接、供应商、采购平台 会同步同一批创建的所有产品*</span>
          </div>
        </div>

        <div class="row" v-if="type == 'modify'">
          <div class="columns">
            <span class="label" style="width: 100px;">是否同步主图:</span>
            <a-radio-group v-model="is_all_pic">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
            <span style="color: red"> *主图 会同步同一批创建的所有产品*</span>
          </div>
        </div>

        <div v-if="type == 'modify' & form.purchase_platform == '1688'" class="row">
          <div class="columns">
            <span class="label">同批标识id:</span>
            <span class="value">{{ form.father_id}}</span>
          </div>
        </div>

        <div v-if="type == 'modify' & form.purchase_platform == '1688'" class="row">
          <div class="columns">
            <span class="label">1688产品id:</span>
            <span class="value">{{ form.productId_1688}}</span>
          </div>
        </div>

        <div v-if="type == 'modify' & form.purchase_platform == '1688'" class="row">
          <div class="columns">
            <span class="label">1688样式id:</span>
            <span class="value">{{ form.specId_1688}}</span>
          </div>
        </div>

        <div v-if="type == 'modify' & form.purchase_platform == '1688'" class="row">
          <div class="columns">
            <span class="label">1688 SKU:</span>
            <span class="value">{{ form.skuId_1688}}</span>
          </div>
        </div>

        <div v-if="type == 'modify'" class="row">
          <div class="columns">
            <span class="label">创建人员:</span>
            <span class="value">{{ form.creator?form.creator.name:"暂无" }}</span>
          </div>
        </div>

        <div class="row" v-if="type == 'modify'" >
          <div class="columns">
            <span class="label">待采购量:</span>
            <span class="value">{{ form.wait_for_purchase_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">在篮量:</span>
            <span class="value">{{ form.in_basket_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">在途量:</span>
            <span class="value">{{ form.in_transit_quantity }}</span>
          </div>
        </div>

        <div class="row" v-if="type == 'modify'">
          <div class="columns">
            <span class="label">库存量:</span>
            <span class="value">{{ form.stock_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">出库量:</span>
            <span class="value">{{ form.out_stock_quantity }}</span>
          </div>
          <div class="columns">
            <span class="label">丢失量:</span>
            <span class="value">{{ form.loss_quantity }}</span>
          </div>
        </div>

        <div v-if="type == 'modify'" class="row">
          <div class="columns">
            <span class="label">创建人员:</span>
            <span class="value">{{ form.creator?form.creator.name:"暂无" }}</span>
          </div>
        </div>
        
        <div v-if="type == 'modify'">
          <h3 style="margin-top: 10px;">关联ozon商品信息</h3>
          <div v-for="(ozon_product, index) in form.ozon_products" :key="index" style="display: flex;">
            <span style="margin-right: 8px;">{{ index+1 }}.</span>
            <div>
              <div class="row">
                <div class="columns">
                  <img :src="ozon_product.primary_image" alt="" style="width: 80px;">
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">id:</span>
                  <span class="value">{{ ozon_product.id }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">名称:</span>
                  <span class="value">{{ ozon_product.name }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">价格:</span>
                  <span class="value">{{ ozon_product.price }}</span>
                </div>
                <div class="columns">
                  <span class="label">货币信息:</span>
                  <span class="value">{{ ozon_product.currency_code }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">offer_id:</span>
                  <span class="value">{{ ozon_product.offer_id }}</span>
                </div>
              </div>

              <div class="row">
                <div class="columns">
                  <span class="label">product_id:</span>
                  <span class="value">{{ ozon_product.product_id }}</span>
                </div>
                <div class="columns">
                  <span class="label">sku:</span>
                  <span class="value">{{ ozon_product.sku }}</span>
                </div>
              </div>
          </div>
        </div>
      </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import {addSystemProduct,modifySystemProduct } from '@/api/operation'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form', 'type'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      sku_front_name: "",
      options: ["pdd","1688","线下"],
      attrList: [],
      purchase_links: this.form.purchase_link?JSON.parse(this.form.purchase_link):[],
      ori_data: "",
      is_all: false,
      is_all_pic: false,
    };
  },
  computed: {
  },
  watch:{
    purchase_links:{
      handler(newValue){
        this.form.purchase_link = JSON.stringify(newValue)
      },
      deep:true
    },
    visible(newValue) {
      this.purchase_links = this.form.purchase_link?JSON.parse(this.form.purchase_link):[]
    }
  },
  methods: {
    confirm() {

      if(this.type == "add"){
        function combine(arr) { 
          let result = []

          function helper(temp, index) { 
            if (index === arr.length) { 
              result.push(temp.join('-'))
              return
            } 
            for (let i = 0; i < arr[index].length; i++) { 
              helper([...temp, arr[index][i]], index + 1)
            } 
          }
           
          helper([], 0)
          return result
        }

        let system_skus = combine(this.attrList.filter(subArray => subArray.length > 0))
          
        addSystemProduct({...this.form,"system_skus":system_skus,"sku_front":this.sku_front_name}).then(
          res => {
            message.success(res.msg)
            this.$emit('update', false);
          }
        )

      }else if(this.type == "modify"){
        modifySystemProduct({...this.form, is_all:this.is_all, is_all_pic:this.is_all_pic}).then(
          res => {
            message.success(res.msg)
            this.$emit('update', false);
          }
        )
      }
    },
    cancel() {
      this.$emit('cancel', false);
    },

    addAttrList(){
      this.attrList.push([])
    },

    delAttrList(){
      this.attrList.pop()
    },

    addAttr(attr){
      attr.push("")
    },
    
    delAttr(attr){
      attr.pop()
    },

    addPurchaseLink(){
      this.purchase_links.push({link:"",msg:""})
    },
    delpurchaseLink(index){
      this.purchase_links.splice(index,1)
    },
    toTop(index){
      const item = this.purchase_links.splice(index, 1)[0];
      this.purchase_links.unshift(item);
    }
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.row .columns .label{
  margin-right: 10px
}

.row .columns .value{
  color: green;
}
</style>